import { Component } from '@angular/core';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../providers/service/values';
import { Functions } from '../../providers/service/functions';
import { CartService } from '../../providers/service/cart-service';
//import { BillingAddress } from '../checkout/billing-address/billing-address';
//import { CheckoutOption } from '../checkout/checkout-option/checkout-option';


@IonicPage()
@Component({
  templateUrl: 'cart.html'
})
export class CartPage {

  cart: any;
  status: any;
  update: any;
  number: any;
  buttonText: any;
  quantity: number;
  public disableCoupon: boolean = false;
  public disableCheckout: boolean = false;

 
  constructor(public nav: NavController, public service: CartService, public values: Values, public params: NavParams, public functions: Functions) {
  	
    this.quantity = 1;
    this.buttonText = this.values.lan.text_apply_coupon;

    this.service.loadCart()
    .then((results) => this.cart = results);
  }

  
  delete(id){
    this.service.deleteItem(id)
    .then((results) => this.cart = results);     
  }

  
  checkout(){
    this.disableCheckout= true;    
    if(this.cart.error_warning != ""){
      this.disableCheckout= false;
      this.functions.showAlert(this.values.lan.text_error, this.cart.error_warning);
    }
    
    else {
     this.service.checkout()
     .then((results) => this.handleCheckout(results));
    }
  }

  
  handleCheckout(results){
    this.disableCheckout= false;
    if(results.logged != null){
      this.service.getBillingAddress()
      .then((results) => this.handleAddress(results));
    }

    else{
      this.nav.push('CheckoutOption');
    }
  }


  handleAddress(results){
    this.nav.push('BillingAddress', results);
  }


  decreaseCart(id, quantity){ 
    this.quantity = parseInt(quantity)-1;

    this.service.updateCart(id, this.quantity)
    .then((results) => this.handleAddToCart(results));
  }

  
  addToCart(id, quantity){
    this.quantity = parseInt(quantity)+1;
   
    this.service.updateCart(id, this.quantity)
    .then((results) => this.handleAddToCart(results));
  }

  

  handleAddToCart(results){
    this.service.loadCart()
    .then((results) => this.cart = results);
  }

  
  getCoupon(cart){
    if(this.validateForm()){
      this.disableCoupon= true;
      this.buttonText= "Loading..."; 
     
      this.service.getCoupon(cart)
      .then((results) => this.handleCoupon(results));
    }
  }
  
  
  handleCoupon(results){
    this.disableCoupon = false;
    this.buttonText= this.values.lan.text_apply_coupon;
  
    if(results.error != undefined){
      this.functions.showAlert(this.values.lan.text_error, results.error);
    }

    else if(results.success != undefined){
      this.service.loadCartOnCoupon()
      .then((results) => this.cart = results);
      this.functions.showAlert(this.values.lan.text_success, results.success);
    }
  }
  


  validateForm(){
    if(this.cart.coupon == undefined || this.cart.coupon == ""){return false}

    return true
  }
  
}



